


















import { Component, Vue } from 'vue-property-decorator';
import { getRecommendList } from '../api';
import { Loading } from '../components';

@Component({
  components: {
    Loading,
  },
})
export default class Melody extends Vue {
  private created() {
    this.loadData();
  }

  private data() {
    return {
      data: null,
      menuOpen: false,
    };
  }

  private toDetail(id: number): void {
    this.$router.push({path: `/melody/detail/${id}`});
  }

  private async loadData(): Promise<void> {
    const res = await getRecommendList({
      page: 1,
      size: 20,
    });
    this.data = res;
  }
}
